.home-bg {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #222;
}

.home-aspect {
  width: 95vw;
  min-width: 0;
  max-width: 1200px;
  height: auto;
  min-height: 0;
  max-height: 95vh;
  aspect-ratio: 4 / 3;
  margin: 2vw auto;
  border-radius: 16px;
  box-shadow: 0 4px 32px rgba(0,0,0,0.15);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  background: linear-gradient(135deg, #f9b233 0%, #c86d3a 100%);
}

@media (max-width: 900px) {
  .home-aspect {
    width: 100vw;
    max-width: 100vw;
    min-width: 0;
    height: auto;
    max-height: 100vh;
    margin: 0;
    border-radius: 0;
    aspect-ratio: unset;
  }
}

@media (min-width: 1024px) and (min-height: 1366px) {
  .home-aspect {
    width: 100vw;
    height: 100vh;
    max-width: none;
    max-height: none;
    aspect-ratio: unset;
    margin: 0;
    border-radius: 0;
  }
}

.home-header-centered {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-top: -100px;
  margin-left: -25px;
  background: none;
  border-bottom: none;
}

.home-logo {
  margin-top: 100px;
  margin-left: 10px;
  width: 390px;
  height: 390px;
  object-fit: contain;
}

.home-org-info {
  flex: 1;
  text-align: center;
  font-size: 1.9rem;
  margin-left: -100px;
  margin-right: -60px;
  margin-top: -30px;
  color: #222;
  font-weight: 700;
}

.home-main {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
  padding-top: 10px;
}

.home-welcome-msg {
  font-size: 5rem;
  font-weight: bold;
  color: #8B1C1C;
  text-align: center;
  margin-bottom: 20px;
  text-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.home-namaste-image {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 20px 0 40px 0;
}

.home-banner-image {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 20px 0 40px 0;
}

.home-banner-img {
  width: 1000px;
  height: 1500px;
  border-radius: 18px;
  margin-bottom: 12px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.10);
  object-fit: cover;
}

.home-namaste-img {
  width: 500px;
  height: 500px;
  border-radius: 18px;
  margin-bottom: 12px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.10);
  object-fit: contain;
}

.home-action-buttons {
  display: flex;
  gap: 40px;
  margin-top: 30px;
}

.home-action-btn {
  padding: 25px 50px;
  font-size: 2.5rem;
  font-weight: bold;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 8px rgba(0,0,0,0.3);
  min-width: 200px;
}

.home-action-btn.register-btn {
  background: #8B1C1C;
  color: white;
}

.home-action-btn.register-btn:hover {
  background: #6B0F0F;
  box-shadow: 0 6px 12px rgba(0,0,0,0.4);
}

.home-action-btn.checkin-btn {
  background: #8B1C1C;
  color: white;
}

.home-action-btn.checkin-btn:hover {
  background: #6B0F0F;
  box-shadow: 0 6px 12px rgba(0,0,0,0.4);
}

@media (max-width: 768px) {
  .home-header-centered {
    flex-direction: column;
    gap: 15px;
    text-align: center;
  }
  
  .home-logo {
    width: 200px;
    height: 200px;
    margin: 20px auto;
  }
  
  .home-org-info {
    margin: 0;
    font-size: 1.2rem;
  }
  
  .home-welcome-msg {
    font-size: 3rem;
    margin-bottom: 30px;
  }
  
  .home-namaste-img {
    width: 350px;
    height: 350px;
  }
  
  .home-action-buttons {
    flex-direction: column;
    gap: 20px;
  }
  
  .home-action-btn {
    padding: 20px 40px;
    font-size: 2rem;
    min-width: 180px;
  }
  
  .home-banner-img {
    width: 400px;
    height: 250px;
  }
} 