.admin-bg {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #222;
}

.admin-aspect {
  width: 95vw;
  min-width: 0;
  max-width: 1200px;
  height: auto;
  min-height: 0;
  max-height: 95vh;
  aspect-ratio: 4 / 3;
  margin: 2vw auto;
  border-radius: 16px;
  box-shadow: 0 4px 32px rgba(0,0,0,0.15);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  background: linear-gradient(135deg, #f9b233 0%, #c86d3a 100%);
}

@media (max-width: 900px) {
  .admin-aspect {
    width: 100vw;
    max-width: 100vw;
    min-width: 0;
    height: auto;
    max-height: 100vh;
    margin: 0;
    border-radius: 0;
    aspect-ratio: unset;
  }
}

@media (min-width: 1024px) and (min-height: 1366px) {
  .admin-aspect {
    width: 100vw;
    height: 100vh;
    max-width: none;
    max-height: none;
    aspect-ratio: unset;
    margin: 0;
    border-radius: 0;
  }
}

.admin-header-centered {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-top: -100px;
  margin-left: -25px;
  background: none;
  border-bottom: none;
}

.admin-logo {
  margin-top: 100px;
  margin-left: 10px;
  width: 390px;
  height: 390px;
  object-fit: contain;
}

.admin-org-info {
  flex: 1;
  text-align: center;
  font-size: 1.9rem;
  margin-left: -100px;
  margin-right: -60px;
  margin-top: -30px;
  color: #222;
  font-weight: 700;
}

.admin-nav-bar {
  width: 100%;
  background: #8B1C1C;
  border-radius: 0 0 12px 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 0 0 8px 0;
  padding: 8px 40px 8px 0;
}

.admin-nav-home-btn {
  background: none;
  color: white;
  border: none;
  padding: 8px 18px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 2rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  gap: 8;
  transition: background-color 0.3s ease;
}

.admin-nav-home-btn:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.admin-main {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  margin-top: 80px;
  padding-top: 40px;
}

.admin-login-title {
  font-size: 3.5rem;
  font-weight: bold;
  color: #8B1C1C;
  text-align: center;
  margin-bottom: 40px;
  text-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.admin-lottie-container {
  display: flex;
  justify-content: center;
  margin-bottom: 40px;
}

.admin-lottie {
  width: 300px;
  height: 300px;
}

.admin-login-form {
  width: 100%;
  max-width: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
}

.admin-form-row {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-bottom: 25px;
}

.admin-form-row label {
  width: 200px;
  text-align: right;
  margin-right: 20px;
  font-weight: bold;
  color: #8B1C1C;
  align-items: center;
  font-size: 2.2rem;
}

.admin-input {
  flex: 1;
  width: 300px;
  min-width: 300px;
  max-width: 100%;
  padding: 15px 20px;
  border-radius: 8px;
  border: 2px solid #ddd;
  background: white;
  font-size: 1.8rem;
  transition: all 0.3s ease;
}

.admin-input:focus {
  outline: none;
  border-color: #8B1C1C;
  box-shadow: 0 0 0 3px rgba(139, 28, 28, 0.1);
}

.admin-error {
  color: #d32f2f;
  margin-bottom: 20px;
  font-size: 1.6rem;
  text-align: center;
  background: rgba(255, 255, 255, 0.9);
  padding: 10px 20px;
  border-radius: 8px;
  border: 2px solid #d32f2f;
}

.admin-login-btn {
  margin: 30px auto 0 auto;
  padding: 25px 50px;
  font-size: 2.5rem;
  font-weight: bold;
  background: #8B1C1C;
  color: white;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 8px rgba(0,0,0,0.3);
  min-width: 200px;
}

.admin-login-btn:hover {
  background: #6B0F0F;
  box-shadow: 0 6px 12px rgba(0,0,0,0.4);
}

.admin-login-btn:disabled {
  background: #ccc;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

@media (max-width: 768px) {
  .admin-header-centered {
    flex-direction: column;
    gap: 15px;
    text-align: center;
    padding: 15px;
    margin-top: 0;
    margin-left: 0;
  }
  
  .admin-logo {
    width: 200px;
    height: 200px;
    margin-top: 20px;
    margin-left: 0;
  }
  
  .admin-org-info {
    margin: 0;
    font-size: 1.4rem;
  }
  
  .admin-login-title {
    font-size: 2.5rem;
    margin-bottom: 30px;
  }
  
  .admin-lottie {
    width: 200px;
    height: 200px;
  }
  
  .admin-form-row {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }
  
  .admin-form-row label {
    width: 100%;
    text-align: left;
    margin-right: 0;
    font-size: 1.8rem;
  }
  
  .admin-input {
    width: 100%;
    min-width: unset;
    font-size: 1.5rem;
  }
  
  .admin-login-btn {
    font-size: 2rem;
    padding: 20px 40px;
    min-width: 180px;
  }
}
