html, body {
  height: 100%;
  overflow-x: hidden;
  overflow-y: hidden;
}

.register-bg {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #222;
}

.register-aspect {
  width: 95vw;
  min-width: 0;
  max-width: 1200px;
  height: auto;
  min-height: 0;
  max-height: 95vh;
  aspect-ratio: 4 / 3;
  margin: 2vw auto;
  border-radius: 16px;
  box-shadow: 0 4px 32px rgba(0,0,0,0.15);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  background: linear-gradient(135deg, #f9b233 0%, #c86d3a 100%);
}

@media (max-width: 900px) {
  .register-aspect {
    width: 100vw;
    max-width: 100vw;
    min-width: 0;
    height: auto;
    max-height: 100vh;
    margin: 0;
    border-radius: 0;
    aspect-ratio: unset;
  }
}

@media (min-width: 1024px) and (min-height: 1366px) {
  .register-aspect {
    width: 100vw;
    height: 100vh;
    max-width: none;
    max-height: none;
    aspect-ratio: unset;
    margin: 0;
    border-radius: 0;
  }
}

.register-header-centered {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-top: -100px;
  margin-left: -25px;
  background: none;
  border-bottom: none;
}

.register-logo {
  margin-top: 100px;
  margin-left: 10px;
  width: 390px;
  height: 390px;
  object-fit: contain;
}

.register-org-info {
  flex: 1;
  text-align: center;
  font-size: 1.9rem;
  margin-left: -100px;
  margin-right: -60px;
  margin-top: -30px;
  color: #222;
  font-weight: 700;
}

.register-nav-bar {
  width: 100%;
  background: #8B1C1C;
  border-radius: 0 0 12px 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 0 0 8px 0;
  padding: 8px 40px 8px 0;
}

.register-nav-home-btn {
  background: none;
  color: white;
  border: none;
  padding: 8px 18px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 2rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  gap: 8;
  transition: background-color 0.3s ease;
}

.register-nav-home-btn:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.register-main {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  margin-top: 80px;
  padding-top: 40px;
}

.register-title {
  font-size: 2.8rem;
  font-weight: bold;
  color: #8B1C1C;
  text-align: center;
  margin-bottom: 30px;
  text-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

/* Removed old banner styling - now using inline approach like CheckinPage */

.register-subtitle {
  font-size: 2.2rem;
  font-weight: bold;
  color: #8B1C1C;
  text-align: center;
  margin-bottom: 40px;
  text-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.register-form-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 80px;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

.register-lottie-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 500px;
}

.register-lottie {
  width: 500px;
  height: 500px;
}

.register-simple-form {
  flex: 1;
  max-width: 500px;
  padding: 0;
}

.form-field {
  margin-bottom: 30px;
}

.form-field label {
  display: block;
  font-size: 2.2rem;
  font-weight: bold;
  color: #8B1C1C;
  margin-bottom: 15px;
}

.form-field input {
  width: 100%;
  padding: 18px 20px;
  font-size: 1.8rem;
  border: 3px solid #ddd;
  border-radius: 12px;
  background: white;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.form-field input:focus {
  outline: none;
  border-color: #8B1C1C;
  box-shadow: 0 0 0 3px rgba(139, 28, 28, 0.1);
}

.form-field input.error {
  border-color: #d32f2f;
}

.error-message {
  color: #d32f2f;
  font-size: 1.3rem;
  margin-top: 8px;
  display: block;
}

.volunteer-field {
  margin-bottom: 35px;
}

.volunteer-field label {
  font-size: 2.2rem;
  font-weight: bold;
  color: #8B1C1C;
  margin-bottom: 15px;
}

.radio-group {
  display: flex;
  gap: 30px;
  margin-top: 15px;
}

.radio-option {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 1.8rem;
  cursor: pointer;
  position: relative;
  color: #8B1C1C;
  font-weight: 600;
}

.radio-option input[type="radio"] {
  width: auto;
  margin: 0;
  opacity: 0;
  position: absolute;
}

.radio-custom {
  width: 24px;
  height: 24px;
  border: 3px solid #8B1C1C;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  background: white;
}

.radio-option input[type="radio"]:checked + .radio-custom::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 12px;
  height: 12px;
  background: #8B1C1C;
  border-radius: 50%;
}

.error-banner {
  background: #ffebee;
  color: #d32f2f;
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 20px;
  font-size: 1.2rem;
  font-weight: bold;
  border: 2px solid #d32f2f;
}

.register-submit-btn {
  width: 100%;
  padding: 25px;
  font-size: 3rem;
  font-weight: bold;
  background: #8B1C1C;
  color: white;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 25px rgba(139, 28, 28, 0.4);
  margin-top: 20px;
}

.register-submit-btn:hover {
  background: #6B0F0F;
  box-shadow: 0 12px 35px rgba(139, 28, 28, 0.5);
  transform: translateY(-2px);
}

.register-submit-btn:disabled {
  background: #ccc;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

/* Success state */
.register-success {
  text-align: center;
  padding: 40px;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  box-shadow: 0 12px 35px rgba(0,0,0,0.25);
  border: 3px solid #f9b233;
  margin: 0 auto;
  max-width: 600px;
  width: 100%;
}

.success-icon {
  font-size: 4rem;
  margin-bottom: 20px;
}

.register-success h2 {
  font-size: 2.5rem;
  color: #2E7D32;
  margin-bottom: 15px;
}

.register-success p {
  font-size: 1.4rem;
  color: #333;
  margin-bottom: 12px;
  line-height: 1.5;
}

.success-details {
  background: rgba(46, 125, 50, 0.1);
  border: 2px solid #2E7D32;
  border-radius: 10px;
  padding: 20px;
  margin: 20px 0;
  text-align: left;
}

.success-details p {
  font-size: 1.3rem;
  color: #2E7D32;
  margin-bottom: 8px;
  font-weight: 500;
}

.success-details p:last-child {
  margin-bottom: 0;
}

.success-button {
  background: #8B1C1C;
  color: white;
  border: none;
  padding: 15px 30px;
  font-size: 1.6rem;
  font-weight: bold;
  border-radius: 10px;
  cursor: pointer;
  margin-top: 20px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(139, 28, 28, 0.3);
}

.success-button:hover {
  background: #6B0F0F;
  transform: translateY(-2px);
}

@media (max-width: 768px) {
  .register-header-centered {
    flex-direction: column;
    gap: 15px;
    text-align: center;
    padding: 15px;
    margin-top: 0;
    margin-left: 0;
  }
  
  .register-logo {
    width: 200px;
    height: 200px;
    margin-top: 20px;
    margin-left: 0;
  }
  
  .register-org-info {
    margin: 0;
    font-size: 1.4rem;
  }
  
  .register-title {
    font-size: 2.2rem;
    margin-bottom: 20px;
  }
  
  .register-sai-baba-img {
    width: 250px;
    height: 250px;
  }
  
  .register-subtitle {
    font-size: 1.8rem;
    margin-bottom: 30px;
  }
  
  .register-form-container {
    flex-direction: column;
    gap: 30px;
  }
  
  .register-lottie {
    width: 250px;
    height: 250px;
  }
  
  .register-simple-form {
    padding: 25px;
  }
  
  .form-field label {
    font-size: 1.5rem;
  }
  
  .form-field input {
    font-size: 1.3rem;
  }
  
  .radio-option {
    font-size: 1.4rem;
  }
  
  .register-submit-btn {
    font-size: 1.8rem;
    padding: 18px;
  }
} 